html {
	overflow-y: scroll;

	&.no-overflow {
		overflow: unset;
	}

	&.js-height {
		height: 100%;
	}
}

img {
	max-width: 100%;
	// width: 100%;
	height: auto;
	max-height: 100%;
	object-fit: contain;
}

body.fixed {
	overflow: hidden;
	// width:calc(100% - 17px);
}

.swiper-container, .swiper-container-three, .swiper-container-two {
	position: relative;
	z-index: 1;
	width: 100%;
	overflow: hidden;
	margin: 0 auto;
	padding: 0 !important;
	list-style: none;
}

.popup {
	display: none;

	.response-popup {
		padding: 1rem;

		&__title {
			font-size: 24px;
			font-weight: 500;
			font-style: normal;
			font-stretch: normal;
			line-height: 1;
			letter-spacing: normal;
			text-align: center;
			color: #2e2e2e;
			margin: 1rem 0;
		}

		&__text {
			font-size: 14px;
			font-weight: normal;
			font-style: normal;
			font-stretch: normal;
			line-height: 1.57;
			letter-spacing: normal;
			text-align: center;
			color: #262626;
			margin-bottom: 1rem;
		}
	}
}

h1.title {
	color: #333333;
	font-family: "Roboto Condensed";
	font-size: 30px;
	font-weight: 400;
	line-height: 44px;
	text-transform: uppercase;
	width: 75%;
	margin-bottom: 20px;
	margin-top: 10px;
}

.container {
	.col-md-9 {
		position: relative;
		padding: 0;

		@media (min-width: 768px) {
			flex: 0 0 calc(75% - 7px);
			max-width: calc(75% - 7px);
			margin-left: 7px;
		}
	}

	&.new-catalog {
		position: relative;
	}
}

.col-md-3 {
	padding-left: 0;
}

div[id^=wait] {
	position: fixed !important;
	top: 50% !important;
	left: 50% !important;
	transform: translateX(-50%) translateY(-50%) !important;
	// <div id="wait_0.4884693106661806"style="background: url(&quot;/bitrix/js/main/core/images/wait.gif&quot;) 10px center no-repeat scroll rgb(252, 247, 209); border: 1px solid rgb(225, 181, 45); color: black; font-family: Verdana, Arial, sans-serif; font-size: 11px; padding: 10px 30px 10px 37px; position: absolute; z-index: 10000; text-align: center; top: 885px; left: 1685px;">...</div>
}

.compensate-for-scrollbar {
	margin-right: 0 !important;
}

body {
	&.fancybox-active {
		overflow: unset !important;
	}

	&.loading-ajax {
		&:before {
			content: "";
			position: fixed;
			z-index: 9999;
			top: 50%;
			left: 50%;
			width: 100vw;
			width: 100vw;
			height: 100vh;
			height: 100vh;
			transform: translateX(-50%) translateY(-50%);
			background: url("../images/blue-loader.svg") rgba(255, 255, 255, .8) no-repeat center 40%;

			@media screen and (max-width: 767px) {
				background: url("../images/blue-loader.svg") rgba(255, 255, 255, .8) no-repeat center;
			}
		}
	}
}

