.BadPols {
	border-color: #e03127 !important;
}

.button-close {
	position: absolute;
	z-index: 10;
	top: 6px;
	right: 7px;
	width: 12px;
	height: 12px;
	background: url("../images/close.svg");
	background-size: 100%;
	cursor: pointer;
}

.hide {
	display: none;
}

picture {
	// justify-content: center;
	display: block;
	width: 100%;
	max-width: 100%;
	max-height: 100%;
	text-align: center;
	// display: flex;
	// align-items: stretch;
	text-align: center;

	img {
		max-width: 100%;
		height: auto !important;
		max-height: 100%;
		object-fit: contain;
	}
}

.go_to_top {
	display: flex;
	position: fixed;
	z-index: 1000;
	right: 25px;
	bottom: 150px;
	-webkit-transform: translate(0, 300px);
	transform: translate(0, 300px);
	color: #474646 !important;
	text-align: center;
	text-decoration: none;

	&:after {
		content: url("../images/go-up-button-normal-blue.svg");
		transition: .3s;
	}
	&:hover {
		&:after {
			content: url("../images/go-up-button-hover-blue.svg");
		}
	}
}


.go_to_top.visible_goto {
	-webkit-transform: translate(0, 0);
	transform: translate(0, 0);
}

.to-top {
	font-size: 20px;
}

.to-top span {
	color: #01426a;
}

@media (max-width: 767px) {
	.go_to_top {
		bottom: 100px;
	}
}

@media screen and (max-width: 767px) {
	h1.title {
		font-size: 30px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 42px;
		letter-spacing: normal;
		text-align: left;
		color: #262626;
		width: 100%;
		background: none;

		&.shipping-payment__title {
			padding: 15px 0;
		}
	}

	.col-md-3 {
		padding-right: 0;
		padding-left: 0;
		padding-top: 14px;

		&.order {
			order: 1;
		}
	}

	.fixed {
		width: 100%;
	}

	// .fancybox-container {
	// 	top: 60px;
	// 	height: calc(100% - 60px);
	// }
}
