.text-default,
.articles-main__info,
.about-store,
.about-brand,
.description {
	margin-bottom: 48px;
	display: flex;
	flex-direction: column;

	img {
		// display: block;
		max-width: 100%;

		// margin-bottom: 28px;
		+p {
			margin-top: 28px;
		}
	}

	h2 {
		color: #333333;
		font-family: "Roboto Condensed";
		font-size: 24px;
		font-weight: 400;
		line-height: 24px;
		margin-bottom: 16px;
		margin-top: 0px;

		b {
			margin: 0;
		}

		&:first-child {
			width: 100%;
		}
	}

	h3 {
		color: #333333;
		font-family: "Roboto Condensed";
		font-size: 20px;
		font-weight: 400;
		line-height: 28px;
	}

	h4,.h4 {
		text-align: left;
		font-size: 22px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 32px;
		letter-spacing: normal;
		color: #394791;
		margin-bottom: 40px;
		font-family: Roboto Condensed,sans-serif;
	}

	h5 {
		color: #333333;
		font-family: "Roboto Condensed";
		font-size: 18px;
		font-weight: 400;
		line-height: 28px;

		b {
			color: $base;
			line-height: inherit;
		}
	}

	b {
		width: 100%;
		margin-bottom: 13px;
		color: #333333;
		font-family: "Roboto Condensed";
		font-size: 18px;
		font-weight: 700;
		line-height: 24px;
		// text-align: left;
		color: #333333;
		margin-top: 18px;
	}

	p {
		// display: block;
		width: 100%;
		color: #333333;
		font-family: Arial;
		font-size: 14px;
		line-height: 22px;
		text-align: left;
		z-index: 99;
		margin: 6px 0 15px;

		a {
			color: #394791;

			&[href^="tel:"] {
				font-size: 14px;
				font-weight: 500;
				font-style: normal;
				font-stretch: normal;
				line-height: 1.57;
				letter-spacing: normal;
				text-align: left;
				color: #262626;
			}
		}

		span {
			width: 100%;
			padding: 5px 0;
			font-family: Arial;
			font-weight: 700;
		}

		strong {
			margin-bottom: 0px;
		}

		&+strong {
			margin-bottom: 10px;
		}

		&+table {
			&+p {
				margin-top: 16px;
			}
		}

		// img {
		// 	margin: 0 auto;
		// 	&:nth-last-child(2):first-child,
		// 	&:nth-last-child(2):first-child~img {
		// 		color: green;
		// 	}
		// }
		img {
			margin-right: 0;
			margin-left: 0;
			margin-bottom: 0px;
		}
	}

	ol {
		color: #333333;
		font-family: Arial;
		font-size: 14px;
		font-weight: 700;
		line-height: 22px;
		padding-left: 0;
		margin-bottom: 20px;
	}

	ul {
		z-index: 99;
		list-style: disc;
		color: #333333;
		font-family: "Roboto Condensed";
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;
		text-align: left;
		color: #333333;
		padding-left: 0;
		width: 100%;
		margin-bottom: 12px;
		margin-top: 0px;
		display: flex;
		flex-direction: column;

		p {
			margin-top: 12px;
		}

		// li:first-child {
		// 	margin-top: 16px;
		// }
		b {
			margin-bottom: 16px
		}

		ul {
			margin-bottom: 0;

			+p {
				margin: 4px 0 12px;

				+p {
					margin: 4px 0 12px;
				}
			}

			li {
				margin-bottom: 2px;
			}
		}
	}

	li {
		color: #333333;
		font-family: Arial;
		font-size: 14px;
		line-height: 20px;
		text-align: left;
		margin-left: 43px;
		font-weight: 400;
		// margin-bottom: 8px;
	}

	strong {
		color: #333333;
		font-family: Arial;
		font-size: 14px;
		font-weight: 700;
		line-height: 22px;
		margin: 5px 0;
	}

	tr {
		z-index: 99;
		list-style: none;
		display: flex;
		font-size: 16px;
		font-weight: 500;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.5;
		letter-spacing: normal;
		text-align: left;
		color: #333333;
		padding-left: 0;
		width: 100%;
		border-bottom: 1px solid #e8e8e8;

		&:first-child {
			td {
				color: #666666;
				font-family: Arial;
				font-size: 12px;
				font-weight: 700;
				padding: 11px 7px 13px;
			}
		}

		td:not(:first-child) {
			flex-grow: 1;
			min-width: 150px;
			flex-shrink: 0;
			max-width: 200px;
			// margin-left: auto;
		}

		p {
			margin: 0;
			width: auto;
		}
	}

	td {
		font-family: "Arial";
		color: #333333;
		font-size: 14px;
		display: flex;
		flex-direction: row;
		padding: 11px 11px 13px;
		align-items: center;
		flex-wrap: wrap;

		&:first-child {
			// width: 286px;
			// flex-shrink: 0;
			margin-right: auto;
			max-width: 575px;
			width: 100%;
		}

		i {
			color: #666666;
			font-family: Arial;
			font-size: 14px;
			font-style: normal;
		}

		b {
			color: #666666;
			font-family: Arial;
			font-size: 12px;
			font-weight: 700;
			padding: 0;
			margin: 0;
		}
	}

	// div:not(.return-exchange__field) {
	// 	display: block;
	// 	// flex-wrap: wrap;
	// 	// justify-content: center;
	// }
	.table {
		width: 100%;
		margin-bottom: 22px;
		margin-top: 19px;

		tbody {
			.table_tr:nth-child(odd) {
				background-color: #f8f8f8;
			}

			.table_td {
				color: #333333;
				font-family: Arial;
				font-size: 14px;
				font-weight: 400;
				padding: 8px 0 7px 13px;

				&:first-child {
					width: unset;
					flex-grow: 1;
				}

				&:last-child {
					text-align: right;
					padding-right: 50px;
				}
			}
		}
	}

	hr {
		width: 100%;
		background: #e2e2e2;
		height: 2px;
	}

	blockquote {
		padding: 24px;
		background-color: #f8f8f8;
		margin-bottom: 2rem;

		b {
			width: 100%;
			margin-bottom: 13px;
			color: #333333;
			font-family: "Roboto Condensed";
			font-size: 18px;
			font-weight: 700;
			line-height: 24px;
			text-align: left;
			color: #333333;
			margin-top: 18px;
		}

		p {
			margin: 0 0 14px;

			b {
				font-size: inherit;
				line-height: inherit;
			}
		}

		strong {
			margin-bottom: 15px;
			display: block;
		}

		ul {
			li {
				color: #394791;
				margin-left: 16px;

				p {
					margin: 0;
				}
			}
		}

		&.column {
			display: flex;
			flex-wrap: wrap;

			ul {
				width: 33%;
			}

			.time {
				margin-top: 14px;

				br {
					display: none;
				}
			}
		}
	}

	.maps {
		padding-top: 18px;
		border-bottom: 1px solid #e2e2e2;
		border-top: 1px solid #e2e2e2;
		margin-bottom: 30px;
	}

	.tags__status {
		width: 100%;
		height: auto;
		border: 1px solid #f1f1f1;
		display: flex;
		flex-direction: column;
		padding: 16px;

		a {
			color: #394791;
			font-family: Arial;
			font-size: 12px;
			margin-right: 26px;
		}
	}

	.container {
		display: block;
	}

	&.seo-text {
		h2 {
			color: #262626;
			font-size: 26px;
			font-weight: 400;
			line-height: 24px;
		}
	}

	.official-dealers {
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		margin-bottom: 29px;

		h2 {
			text-align: center;
			width: 100%;
			margin-bottom: 18px;
			margin-top: 18px;
		}

		p {
			margin-bottom: 10px;
		}

		img {
			margin-bottom: 0;
		}

		// .official-dealers__link
		&__link {
			width: calc(50%-16px);
			display: flex;
			position: relative;
			overflow: hidden;
			margin-bottom: 19px;
			margin-top: 29px;
			flex-direction: column;

			&:nth-of-type(1) {
				margin-right: 29px;
			}

			&:after {
				content: '';
				background-image: url('../images/play.svg');
				position: absolute;
				background-repeat: no-repeat;
				background-size: 40px 40px;
				background-color: rgba(0, 0, 0, 0.5);
				background-position: center center;
				left: 0;
				top: 0;
				width: 100%;
				height: calc(100% - 36px);
			}

			&:hover {
				img {
					transform: scale(1.1);
				}
			}

			span {
				color: #333333;
				font-family: "Roboto Condensed";
				font-size: 18px;
				font-weight: 400;
				line-height: 26px;
				background: white;
				width: 100%;
				// margin-top: 16px;
				height: 45px;
				display: flex;
				align-items: flex-end;
				z-index: 9;
			}
		}
	}

	.about-store__button,
	.seo-text__button,
	.about-brand__button {
		display: none;
	}

	.mobile {
		display: none;
	}

	.pic_left {
		float: left;
		margin: 5px 25px 20px 0;
	}

	.pic_right {
		float: right;
		margin: 5px 0 25px 20px;
	}

	.table_delivery {
		tr {
			td:last-child {
				b {
					font-family: Arial;
					color: #333;
					font-size: 14px;
					font-weight: 400;
				}
			}

			td {
				padding: 11px 7px 13px;

				&[colspan="2"] {
					b {
						font-size: 14px;
						font-weight: 700;
					}
				}
			}
		}
	}

	.page {
		&__title {
			font-size: 28px;
			line-height: 28px;
			font-weight: 300;
			margin-bottom: 10px;
			color: #000;

			&_detail {
				display: table;
				margin: -29px 0 0;
				height: 112px;
				margin-bottom: 15px;
			}
		}

		&__cell {
			vertical-align: middle;
			display: table-cell;
		}

		&__table {
			border-collapse: separate;
			border-spacing: 2px;
			margin-bottom: 20px;

			tr {
				display: table-row;
			}

			th {
				padding: 5px;
				border: 1px solid #646d7f;
			}

			td {
				display: table-cell;
				padding: 5px;
				border: 1px solid #646d7f;
				// display: table-row;
			}
		}

		&__section {
			border-radius: 2px;
			padding: 5px 10px;
			font-size: 90%;
			border: 1px solid #0D74BC;
			color: #0D74BC;
			margin-bottom: 20px;

			&_red {
				color: #ee1d24;
				border: 1px solid #ee1d24;
			}

			p {
				margin: 0 0 2px;
			}
		}

		&__date {
			position: relative;
			// color: @link_color;
			font-weight: 500;
			pointer-events: none;
			margin-bottom: 20px;

			// top: -22px;
			noindex+& {
				top: -60px;
				margin-bottom: 0;
			}
		}
	}

	@media screen and (max-width: 767px) {
		margin-bottom: 30px;

		img {
			// max-height: 52px;
			margin-top: -5px;
			margin-bottom: 24px;
		}

		h3 {
			font-size: 18px;
			font-weight: 400;
			line-height: 24px;
			margin-bottom: 16px;
		}

		h4,.h4 {
			font-size: 16px;
			font-weight: 400;
			line-height: 25px;
			margin-bottom: 23px;
			padding-top: 0;
		}

		h5 {
			font-size: 16px;
			font-weight: 400;
			line-height: 24px;
		}

		p {
			font-size: 14px;
			line-height: 22px;
			margin-bottom: 24px;

			&+table+p {
				margin-top: 16px;
				margin-bottom: 35px;
			}

			&:last-child {
				margin-bottom: 0;
			}
		}

		b {
			width: 100%;
			margin-bottom: 11px;
			font-size: 16px;
			font-weight: 400;
			line-height: 24px;
			margin-top: 0;
		}

		h2 {
			color: #333333;
			font-family: "Roboto Condensed";
			font-size: 22px;
			font-weight: 400;
			line-height: 30px;
		}

		ol {
			margin-bottom: 0;

			li {
				margin-left: 25px;
				line-height: 21px;
			}
		}

		ul {
			font-size: 16px;
			font-weight: 400;
			line-height: 24px;
			margin-bottom: 12px;
			letter-spacing: -0.15px;
		}

		li {
			color: #262626;
			font-size: 14px;
			margin-bottom: 4px;
			margin-left: 31px;
		}

		&.seo-text {
			h2 {
				color: #262626;
				font-size: 22px;
				font-weight: 400;
				line-height: 26px;
				margin-bottom: 16px;
			}

			img {
				padding: 0 !important;
				margin: 0 -15px;
				width: calc(100% + 30px);
				max-width: calc(100% + 30px);
			}
		}

		.official-dealers {
			&__link {
				width: auto;
				display: flex !important;
				position: relative;
				overflow: hidden;
				margin-bottom: 5px;
				margin-top: 14px;
				order: 1 !important;
				width: calc(100% + 32px);

				@media screen and (max-width: 480px) {
					margin: 0 -16px 12px !important;
				}

				&:nth-of-type(1) {
					margin-right: 0px;
				}

				.official-dealers__pic {
					margin: 0 0 0px !important;
					height: auto !important;
				}

				span {
					color: #333333;
					font-family: "Roboto Condensed";
					font-size: 16px;
					font-weight: 400;
					line-height: 26px;
					padding: 0 20px 0 12px;
					height: auto;
				}

				&:after {
					height: calc(105% - 36px)
				}
			}

			h2 {
				text-align: left;
			}

			p,
			ul {
				display: none;
			}

			.mobile {
				display: block;
				color: #262626;
				font-family: Arial;
				font-size: 14px;
				line-height: 22px;
				margin-bottom: 16px;
			}

			a {
				display: block;
			}

			&.active {

				p,
				ul {
					display: block;
				}

				.mobile {
					display: none;
				}
			}
		}

		tr {
			&:first-child {
				td {}
			}
		}

		td {
			// width: 50% !important;
			flex-shrink: 0;
			align-items: center;
			min-width: unset;

			&:first-child {
				width: 180px !important;
				padding-left: 0;
				flex-shrink: 0;
				margin-right: 0;
			}

			&:last-child {
				margin-left: unset;
				flex-wrap: wrap;
				padding-left: 11px;

				p {
					line-height: 1.2;
				}
			}

			i {
				font-size: 12px;
			}
		}

		tr:first-child td:last-child {
			padding-left: 11px;
			max-width: unset;
			margin-left: auto;
		}

		tr:first-child td:first-child {
			padding-left: 0
		}

		.table {
			margin-top: 0;
			width: 100%;

			tbody {
				.table_tr:nth-child(odd) {}

				.table_tr {
					display: flex;
					flex-direction: column;
					padding: 9px 14px;
				}

				.table_td {
					font-family: Arial;
					font-size: 14px;
					font-weight: normal;
					font-style: normal;
					font-stretch: normal;
					letter-spacing: normal;
					color: #333333;
					width: unset;
					flex-shrink: 1;
					padding: 0;
					text-align: left;
					width: unset !important;

					&:first-child {
						margin-bottom: 10px;
					}

					&:last-child {
						flex-shrink: 0;
						text-align: left;
						margin-left: 0;
					}
				}
			}
		}

		blockquote {
			padding: 16px;
			margin-bottom: 35px;

			ol {
				margin-bottom: 10px;

				&+ol {
					margin-bottom: 0;
				}
			}

			a {
				margin-top: 22px;
			}

			&.column {
				ul {
					width: 100%;
					margin-bottom: 0;

					&:last-of-type {
						margin-bottom: 16px;
					}
				}

				.time {
					b {
						display: block;
					}

					br {
						display: block;
					}
				}
			}
		}

		.maps {
			margin-bottom: 20px;
		}

		.maps iframe {
			max-height: 260px;
		}

		table {
			display: block;
			overflow-x: auto;

			&.page__table {
				tbody {
					display: table-row-group;

					tr td:not(:first-child) {
						min-width: unset;
					}

					td:first-child {
						width: auto !important;
						padding-left: 5px;
						-ms-flex-negative: 0;
						flex-shrink: 0;
						margin-right: 0;
					}

					tr:first-child td:first-child {
						padding-left: 7px;
					}
				}
			}

			tbody {
				display: flex;
				max-width: 1176px;
				overflow-x: scroll;
				width: 480px;
				width: max-content;
				flex-direction: column;
			}
		}

		&.seo-text {
			// height: 700px;
			// overflow: hidden;
			position: relative;

			.container {
				display: flex;

				&:first-child {

					:nth-child(5),
					:nth-child(6) {
						display: none;
					}

					:nth-child(-n+6) {
						order: -1;
					}

					&.active {

						:nth-child(5),
						:nth-child(6) {
							display: block;
						}
					}
				}
			}
		}

		.seo-text__button {
			color: #394791;
			font-family: Arial;
			font-size: 14px;
			display: none;
			padding-bottom: 2px;
			border-bottom: 1px dashed #394791;
			width: fit-content !important;
			position: relative;
			// left: 16px;
			// bottom: 13px;
			// width: 100%;
			// padding: 0 16px;
			height: auto;
			opacity: 1;
			display: block;
			margin: 0 16px 14px 0;
			height: auto;
			opacity: 1;

			&:after {
				background: url('https://icongr.am/entypo/chevron-small-down.svg?color=2e2e2e&size=50') no-repeat;
				content: '';
				width: 20px;
				height: 20px;
				position: absolute;
				background-size: contain;
				top: 50%;
				transform: translateY(-50%);
				right: -22px;
			}
		}

		&.active {
			height: auto;

			.about-store__button {
				position: static;

				&:before {
					width: 63px;
				}

				&:after {
					content: '';
					background: url("https://icongr.am/entypo/chevron-up.svg?color=666666") center/16px no-repeat;
					left: 80px;
				}
			}
		}
	}
}
